<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
  export default {
  name: 'radarCircle',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
    }
  },
  watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
  },
  mounted() {
    this.$nextTick(()=>{
         this.myChart = this.$echarts.init(this.$refs.chart);
      this.initChart()
    })
  },
  methods: {
   initChart() {
      let option = {
            backgroundColor:'#fff',
            tooltip: {
                backgroundColor:'rgba(255,255,255,.9)',
                padding:10,
                borderColor:'rgba(211,221,231,.9)',
                borderWidth:1,
                textStyle:{
                    color:'#666'
                }
            },
            color:['#015293','#ff8e14'],
            legend: {
                top:'10',
                right:'20',
                textStyle:{
                    color:'#6a6a6a'
                },
                data: this.echartsData.legendData,
            },
                grid: {
                left: 0,
                right: 0,
                top: 0,
                bottom: 0,
                containLabel: true
            },
            radar: {
                shape: 'circle',
                splitNumber: 4,
                startAngle:-60,
                axisLine: {            // 坐标轴线
                    show: true,        // 默认显示，属性show控制显示与否
                    lineStyle:{
                        width:3,
                        color:'#3d3d3d',
                        shadowColor: 'rgba(61, 61, 61, .28)',
                        shadowBlur: 10,
                        opacity:.08
                    }
                },
                indicator: this.echartsData.indicatorData,
                splitLine:{
                    lineStyle:{
                        color:['rgba(226, 226, 226, .2)','rgba(216, 214, 214, .2)'],
                        width:2
                    },
                    shadowColor:'rgba(0, 0, 0, 1)',
                    shadowBlur: 10
                },
                splitArea:{
                    areaStyle:{
                        color:['rgba(226, 226, 226, .2)','rgba(243, 243, 243, .2)']
                    }
                },
                name: {
                    color: '#474747',
                    fontSize:14,
                    formatter: function(param){
                        return param;
                    }
                }
            },
            series: [{
                name: '',
                type: 'radar',
                symbol: 'circle',
                symbolSize: 6,
                symbolOffset: [0, '60%'],
                data : [
                    {
                        value : this.echartsData.seriesData.enterpriseValue,
                        lineStyle: {
                            normal: {
                                type: 'solid',
                                color:'rgba(1, 82, 147, .5)',
                                width:'2'
                            }
                        },
                        areaStyle: {
                            normal: {
                                color: 'rgba(60, 162, 240, 0.2)'
                            }
                        },
                        itemStyle:{
                            normal:{
                                color:'#14aaff' 
                            }
                        },
                        name : this.echartsData.legendData[0]
                    },
                    {
                        value : this.echartsData.seriesData.averageValue,
                        lineStyle: {
                            normal: {
                                type: 'solid',
                                color:'rgba(255, 126, 0, .5)',
                                width:'2'
                            }
                        },
                        areaStyle: {
                            normal: {
                                color: 'rgba(255, 156, 0, 0.2)'
                            }
                        },
                        itemStyle:{
                            normal:{
                                color:'#ff8e14' 
                            }
                        },
                        name : this.echartsData.legendData[1]
                    }
                ]
            }]
      };
      this.myChart.on('click',(params) =>{
          this.$emit('handleCharts',params.name)
      })
      this.myChart.setOption(option);

    }

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



