<script>
import moduleWrap from 'components/moduleWrap'
import singleBar from 'components/echarts/knownSingleBar'
export default {
  name: 'complementationOfIndustrial',
  data(){
	  return{
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      value: '',
      bar:[
        {
          name:"制造业",
          icon:"fa-camera-retro"
        },{
          name:"烟草制造业",
          icon:"fa-camera-retro"
        },{
          name:"酿酒工业",
          icon:"fa-camera-retro"
        },{
          name:"水泥产业",
          icon:"fa-camera-retro"
        },{
          name:"汽车及零部件产业",
          icon:"fa-camera-retro"
        },{
          name:"铁路装备产业",
          icon:"fa-camera-retro"
        },{
          name:"煤化工产业",
          icon:"fa-camera-retro"
        },{
          name:"服务业",
          icon:"fa-camera-retro"
        },{
          name:"新兴产业",
          icon:"fa-camera-retro"
        }
      ],
      click:[false,true,false,false,false,false,false,false,false],
      name:"烟草制造业",
      option1:{},
      option2:{},
      mediaNumberData: {//媒体来源排行
          xAxisData: [],
          seriesData: [],
          data: [],
          option: {}
        },
    }
  },
  methods: {
    handleLiClick(item, index) {
      this.click = [false, false, false, false, false, false, false, false, false];
      this.click[index] = true;
      this.name = item.name;
    },
    getEs1Data(){
      let myChart = this.$echarts.init(document.getElementById('es1'))
      let scale = 1;
      let echartData = [{
        value: 9,
        name: '竞争分析',
      },{
        value: 29,
        name: '环境分析'
      },{
        value: 28,
        name: '机构分析'
      },
      {
        value: 13,
        name: '企业专家'
      }, {
        value: 7,
        name: '本体分析'
      },
      {
        value: 14,
        name: '企业家'
      }];
      let rich = {
        yellow: {
          fontSize: 15 * scale,
          padding: [10, 0],
          align: 'center'
        },
        total: {
          color: "#ffc72b",
          fontSize: 15 * scale,
          align: 'center'
        },
        white: {
          color: "#adadad",
          align: 'center',
          fontSize: 15 * scale,
          padding: [10, 0]
        },
        hr: {
          borderColor: '#ebebeb',
          width: '100%',
          borderWidth: 1,
          height: 1
        }
      };
      this.option1 = {
        backgroundColor: '#fff',
        series: [{
          name: '',
          type: 'pie',
          radius: ['25%','50%'],
          itemStyle: {
            normal: {
              borderColor: '#fff',
              borderWidth: 2
            }
          },
          hoverAnimation: true,
          color:['#ff9400', '#3371ca', '#1894f9', '#00bbff', '#54d512','#ffca00'],
          label: {
            normal: {
              formatter: function(params, ticket, callback) {
                let total = 0; //总数量
                let percent = 0; //占比
                echartData.forEach(function(value, index, array) {
                  total += value.value;
                });
                percent = ((params.value / total) * 100).toFixed(1);
                return '{yellow|' + percent + '%}\n{hr|}'+ '\n{white|' + params.name + '}';
              },
              rich: rich
            },
          },
          labelLine: {
            normal: {
              length: 25 * scale,
              length2: 2,
              lineStyle: {
                color: '#ebebeb'
              }
            },
            emphasis:{
              show:true,
              length: 35 * scale,
              length2: 1,
              lineStyle: {
                color: '#ebebeb'
              }
            }
          },
          data: echartData
        }]
      };
      myChart.setOption(this.option1);
    },
    getmediaNumberData() {
      this.mediaNumberData.xAxisData = ['减震器','轮胎','仪表板','离合器','刹车片','变速箱','发动机'];
      this.mediaNumberData.seriesData = [20, 30, 40, 20, 30, 90, 50]
    },
  },
  mounted(){
    this.getEs1Data()
    //媒体来源排行
    this.getmediaNumberData()
  },
  components: {
    moduleWrap,
    singleBar
  }
}
</script>

<style lang="scss" scoped>
