    height: 30px;
    width: 50%;
    background-color: transparent;
    border-right: 1px dashed #000;
  }
}
.third {
  display: flex;
  justify-content: center;
  align-items: stretch;
  .text {
    height: 30px;
    width: 10%;
    background-color: transparent;
    border: 1px dashed #000;
    border-bottom: none;
    & + .text {
      margin-left: -1px;
    }
  }
}
.forth {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .text {
    background-color: #ffffff;
