<template>
	<div id="">
		<ul>
			<router-link class="item" tag="li" to="/industryChain/distributionManagement">
				产业链分布管理
			</router-link>
			<router-link class="item" tag="li" to="/industryChain/complementationOfIndustrial">
				产业链结构互补
			</router-link>
			<router-link class="item" tag="li" to="/industryChain/upstreamAndDownstream">
				产业链上下游企业查询
			</router-link>
			<router-link class="item" tag="li" to="/industryChain/occupationRatioRAnalysis">
				行业占比分析
			</router-link>
			<router-link class="item" tag="li" to="/industryChain/industryAnalysisReport">
				行业分析报告
			</router-link>
		</ul>
	</div>
</template>

<script>
	export default {
		name: 'navMenu',
	}
</script>

<style lang="scss" scoped>
div {
	padding: 20px 0;
	ul {
			width: 100%;
			display: flex;
			/*justify-content: flex-end;*/
			li {
				color: #015293;
				padding: 13px 0;
				font-size: 16px;
				cursor: pointer;
				flex: 1;
				text-align: center;
				box-sizing: border-box;
				border: 1px solid #d5d5d5;
				background: linear-gradient(to bottom, #f6f6f6, #dfdfdf);
				&.active {
					background: linear-gradient(to bottom, #0f82c4, #015293);
					border: 1px solid #015293;
					color: #fff;
				}
				&:first-of-type{
					border-radius: 5px 0 0 5px;
				}
				&:last-of-type{
					border-radius: 0 5px 5px 0;
				}
			}
	}
}

</style>


