<template>
		<div class="content">
        <ul class="con-left">
          <li v-for="(item,index) in bar" :class="click[index]?'active':''" @click="handleLiClick(item,index)">
            <i class="fa" :class="item.icon"></i>
            <span>{{item.industry}}</span>
          </li>
        </ul>
        <div class="con-right">
          <div class="crL">
            <div class="es3-div" id="es3">

            </div>
          </div>
          <div class="crR">
            <div class="crR-t">
              <p><span class="bg"></span><span class="title">KSF占比分析</span></p>
              <div class="es1-div" id="es1">

              </div>
            </div>
            <div class="crR-b">
              <p><span class="bg"></span><span class="title">产业供需趋势</span></p>
              <div class="es2-div" id="es2">

              </div>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
import es from 'echarts';
import * as api from '../../api/api';
import mapjson from '../../../static/610300';
require('echarts/map/js/china.js');
export default {
	name: 'distributionManagement',
  data(){
	  return{
      bar:[
        {
          industry:"制造业",
          icon:"fa-pinterest"
        },{
          industry:"烟草制造业",
          icon:"fa-fire-extinguisher"
        },{
          industry:"酿酒工业",
          icon:"fa-beer"
        },{
          industry:"水泥产业",
          icon:"fa-camera-retro"
        },{
          industry:"汽车及零部件产业",
          icon:"fa-car"
        },{
          industry:"铁路装备产业",
          icon:"fa-bus"
        },{
          industry:"煤化工产业",
          icon:"fa-sheqel"
        },{
          industry:"服务业",
          icon:"fa-th-large"
        },{
          industry:"新兴产业",
          icon:"fa-hacker-news"
        }
      ],
      click:[false,true,false,false,false,false,false,false,false],
      params:{
        industry:"烟草制造业",
        area:""
      },
      option1:{},
      option2:{},
      option3:{},
    }
  },
  mounted(){
	  this.init();
  },
  methods: {
    handleLiClick(item, index) {
      this.click = [false, false, false, false, false, false, false, false, false];
      this.click[index] = true;
      this.params.industry = item.industry;
    },
    init(){
      this.getEs1Data();
      this.getEs2Data();
      this.getEs3Data()
    },
    getEs1Data(){
      console.log("获取占比分析");
      let myChart = es.init(document.getElementById('es1'));
      let scale = 1;
      let echartData = [
        {
        value: 9,
        name: '竞争分析',
      },{
        value: 29,
        name: '环境分析'
      },{
        value: 28,
        name: '机构分析'
      },
      {
        value: 13,
        name: '企业专家'
      }, {
        value: 7,
        name: '本体分析'
      },
      {
        value: 14,
        name: '企业家'
      }];
      let rich = {
        yellow: {
          fontSize: 14,
          padding:[0,-30,0,-25],
          align: 'left',
          lineHeight:15,
          fontWeight:'bold',
        },
        black: {
          color: '#666666',
          fontSize: 12,
          padding:[0,-15,0,-25],
          align: 'left',
          lineHeight:15,
        }
      };
      this.option1 = {
        backgroundColor: '#fff',
        color:['#ff9400', '#3371ca', '#1894f9', '#00bbff', '#54d512','#ffca00'],
        calculable: false,
        series: [{
          name: '注销企业数量',
          type: 'pie',
          center: ['50%', '55%'],
          radius: ['32%', '55%'],
          label: {
            normal: {
              position: 'outside',
              show: true,
              formatter: function (params) {
                return '{yellow|' + params.value + '%}\n{black|' + params.name + '}'
              },
              textStyle: {
                color: "#333",
                fontSize: 10
              },
              padding: [3, -40, 45, -40],
              rich: rich
            },
            emphasis: {
              position: 'outside',
              show: true,
              formatter: function (params) {
                return '{yellow|' + params.value + '%}\n{black|' + params.name + '}'
              },
              textStyle: {
                color: "#333",
                fontSize: 10
              },
              rich: rich
            }
          },
          itemStyle: {
            normal: {
              borderColor: '#fff',
              borderWidth: 2
            }
          },
          labelLine: {
            normal: {
              textStyle: {
                fontSize: 10
              },
              smooth: false,
              length:5,
              length2: 60,
              lineStyle: {
                width: 2,
                color: '#ebebeb'
              }
            },
            emphasis: {
              textStyle: {
                fontSize: 10
              },
              length: 20,
              length2: 20,
              lineStyle: {
                width: 2,
                color: '#ebebeb'
              }
            }
          },
          data:[{
            value: 9,
            name: '竞争分析',
          }, {
            value: 29,
            name: '环境分析'
          }, {
            value: 28,
            name: '机构分析'
          },
            {
              value: 13,
              name: '企业专家'
            }, {
              value: 7,
              name: '本体分析'
            },
            {
              value: 14,
              name: '企业家'
            }],
        }]
      }
        myChart.setOption(this.option1);
    },
    getEs2Data(){
      console.log("获取产业供需趋势");
      let myChart = es.init(document.getElementById('es2'));
      let data1 = [94, 88, 97, 88, 83, 95];
      let data2 = [102, 99, 100, 103, 105, 100];
      let data3 = ['2013', '2014', '2015', '2016', '2017', '2018'];
      this.option2 = {
        backgroundColor: '#fff',
        tooltip: {
          trigger: 'axis',
          backgroundColor: '#fff',
          shadow:10,
          padding: 10,
          shadowBlur:10,
          shadowColor: 'rgba(211, 211, 231, .9)',
          borderColor: 'rgba(211,221,231,.9)',
          borderRadius:5,
          borderWidth: 1,
          textStyle:{
            color:'#666',
          }
        },
        legend: {
          show: true,
          data: ['产量', '消费'],
          left: 'center',
          top: '20',
          textStyle: {
            color: '#333',
            fontSize: 14
          },
          itemGap:33,
          itemWidth:20,
          itemHeight: 10,
        },
        grid: {
          left: '3%',
          right: '5%',
          bottom: '10%',
          containLabel: true,
          backgroundColor:'#fafdff',
          show:true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          data: data3,
          axisLabel:{
            color: '#333',
            textStyle:{
              fontSize: 12
            },
            margin:20,
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#d6d7d8',
              width: 1,
            }
          },
          axisTick:{
            show:false
          },
          splitLine:{
            lineStyle:{
              color:'#edf2f4',
              type:'solid',
              width: 1,
            }
          }
        }],
        yAxis: [{
          min:80,
          max:120,
          type: 'value',
          axisLabel:{
            color: '#333',
            textStyle:{
              fontSize: 12
            },
            margin:20,
          },
          axisLine: {
            show: true,
            lineStyle: {
              color: '#d6d7d8',
              width: 1,
            }
          },
          axisTick:{
            show:false
          },
          splitLine:{
            lineStyle:{
              color:'#edf2f4',
              type:'solid',
              width: 1,
            }
          }
        }],
        series: [{
          name: '产量',
          type: 'line',
          itemStyle: {
            normal: {
              color: '#35a7fa',
              borderColor: '#fff',
              borderWidth: '2',
            }
          },
          symbol:'circle',
          symbolSize:8,
          areaStyle:{
            normal:{
              color: new es.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: 'rgba(215,241,255,.5)'
              },
                {
                  offset: 1,
                  color: 'rgba(212,240,255,.5)'
                }])
            }
          },
          smooth: true,
          data: data1
        },
          {
            name: '消费',
            type: 'line',
            itemStyle: {
              normal: {
                color: '#ff8800',
                borderColor: '#fff',
                borderWidth: '2',
              }
            },
            symbol:'circle',
            symbolSize:0,
            smooth: true,
            data: data2
          }
        ]
      };
      myChart.setOption(this.option2);
    },
    getEs3Data() {
      console.log("获取地图数据");
      let echartData = [
          {name: '凤县', value:[107.400737,34.721218],val: '扶风工业区'},
          {name: '凤县', value:[107.400737,34.821218],val: '扶风工业区'},
      ];
      let that = this;
      let myChart = es.init(document.getElementById('es3'));
      es.registerMap('宝鸡', mapjson);
      this.option3 = {
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(255,255,255,0.9)',
          borderColor: '#fff',
          textStyle: {
            color: '#333',
            fontSize: 14,
            width:50,
            height:40,
          },
          borderWidth: 1,
          position: 'top',
          formatter: function (params) {
            return '<div style="position: relative;"><span style="color:#333; font-weight:bold; font-size:16px;">'+params.data.val+'</span>'+
              '<p style="width: 10px;height: 10px;border-radius:2px;background:rgba(255,255,255,0.9);transform: rotate(45deg);position: absolute;top: 0;left: 0;bottom:0 ;right: 0;margin: auto;margin-top: 30px"></p></div>';
          },
          padding: [12, 20]
        },
        geo: {
          map: '宝鸡',
          top:70,
          label: {
            emphasis: {
              show: true
            }
          },
          roam: true,
          itemStyle: {
            normal: {
              borderColor: '#a2d6f3'
            },
            emphasis: {
              borderColor: '#a2d6f3'
            }
          }
        },
        series: [
          {
            type: 'map',
            mapType: '宝鸡',
            showLegendSymbol: false,
            zoom: 1.25,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  textStyle: {
                    color: "#545759"
                  }
                },
                borderColor: '#5bbbfc',
                borderWidth: 2,
                areaColor: '#c8e6ff',
                shadowColor: 'rgba(83, 190, 255, .3)',
                shadowBlur: 5,
                shadowOffsetX: 5,
                shadowOffsetY: 3,
              },
              emphasis: {
                areaColor: 'rgba(60, 145, 214, .7)',
                borderColor: '#5bbbfc',
                borderWidth: 2,
                borderType: 'solid',
              }
            },
            tooltip:{
              show:false,
            },
            data: [
              {name: '金台区', value: 610303},
              {name: '渭滨区', value: 610302},
              {name: '陈仓区', value: 610304},
              {name: '凤翔县', value: 610322},
              {name: '岐山县', value: 610323},
              {name: '扶风县', value: 610324},
              {name: '眉县', value: 610326},
              {name: '陇县', value: 610327},
              {name: '千阳县', value: 610328},
              {name: '麟游县', value: 610329},
              {name: '太白县', value: 610331},
              {name: '凤县', value: 610330},
            ],
          },
          {
            type: 'scatter',
            coordinateSystem: 'geo',
            // symbol: 'image://../../assets/img/positionIcon.png',
            symbol:'pin',
            data:echartData,
            symbolSize: 22,
            label: {
              normal: {
                show: false,
                fontWeight:'bold',
                fontSize:14,
              },
              emphasis: {
                show: false
              }
            }
          }
        ]
      };
      myChart.setOption(this.option3);
      myChart.on('click', function (params){
        that.params.area = params.data.name
      });
    }
  },
  computed: {
    industry() {
      return this.params.industry
    },
    area(){
      return this.params.area
    }
  },
  watch: {
    industry(newValue, oldValue){
      this.init()
    },
    area(newValue, oldValue){
      this.getEs1Data();
      this.getEs2Data();
    }
  },
}

</script>

<style scoped lang="scss">
.content{
  width: 1200px;
  margin: 0 auto 20px;
  background: #fff;
  overflow: hidden;
  .con-left{
    width: 220px;
    height: 810px;
    border: 1px solid #dedede;
    background: #f3f3f3;
    float: left;
    li {
      height: 49px;
      line-height: 49px;
      padding-left: 40px;
      font-size: 14px;
      border-bottom: 1px solid #dadada;
      i {
        color: #9e9e9e;
        margin-right: 6px;
      }
    }
    li:hover{
      cursor: pointer;
    }
    .active{
      background: #0662a3;
      color: #fff;
      i{
        color: #fff;
      }
    }
  }
  .con-right{
    width: 967px;
    height: 810px;
    border: 1px solid #dedede;
    background: #f8f8f8;
    float: right;
    .crL{
      width: 560px;
      height: 100%;
      float: left;
      .es3-div{
        width: 560px;
        height: 520px;
        margin-top: 115px;
      }
    }
    .crR{
      float: left;
      .crR-t{
        margin-top: 30px;
        margin-bottom: 35px;
        p{
          height: 16px;
          line-height: 16px;
          margin-bottom: 10px;
          .bg{
            display: inline-block;
            vertical-align: middle;
            width: 4px;
            height: 16px;
            background: #005095;
            margin-right: 10px;
          }
          .title{
            font-size: 12px;
            display: inline-block;
            font-weight: bold;
          }
        }
        .es1-div{
          width: 381px;
          height: 362px;
          border: 1px solid #ccc;
          background: #fff;
        }
      }
      .crR-b{
        p{
          height: 16px;
          line-height: 16px;
          margin-bottom: 10px;
          .bg{
            display: inline-block;
            vertical-align: middle;
            width: 4px;
            height: 16px;
            background: #005095;
            margin-right: 10px;
          }
          .title{
            font-size: 12px;
            display: inline-block;
            font-weight: bold;
          }
        }
        .es2-div{
          width: 381px;
          height: 292px;
          border: 1px solid #ccc;
          background: #fff;
        }
      }
    }
  }
}
</style>



