<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
  export default {
  name: 'perPie',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {}
  },
  watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
  },
  mounted() {
    this.$nextTick(()=>{
         this.myChart = this.$echarts.init(this.$refs.chart);
      this.initChart()
    })
  },
  methods: {
   initChart() {
      let option = {
             title:{
                text:this.echartsData.item?this.echartsData.item.titleName:'',
                top:15,
                left:15,
                textStyle:{
                    color:'#333',
                    fontSize:14
                }
            },
            series: [{
                name: '占比',
                type: 'pie',
                radius: ['45%', '60%'],
                avoidLabelOverlap: true,
                silent: true,
                label: {
                    normal: {
                        position: 'center'
                    }
                },
                data: [{
                        value: this.echartsData.item?this.echartsData.item.possession[0]:'',
                        name: '未发生',
                        itemStyle: {
                            normal: {
                                color: '#b9d1f8',
                                borderColor: '#fff',
                                borderWidth: 2,
                            }
                        },
                        label: {
                            normal: {
                                formatter:this.echartsData.item?this.echartsData.item.proportion:'',
                                textStyle: {
                                    fontSize: this.echartsData.item.smallPic?16:20,
                                    color: '#397ad4'
                                }
                            },
                            lineHeight: 6,
                        }
                    },
                    {
                        value: this.echartsData.item?this.echartsData.item.possession[1]:'',
                        name: this.echartsData.item?this.echartsData.item.proportionType:'',
                        itemStyle: {
                            normal: {
                                barBorderRadius: 20,
                                color: new this.$echarts.graphic.LinearGradient(0, 0, 1, 1, [{
                                    offset: 0,
                                    color: this.echartsData.item?this.echartsData.item.changeColor[0]:''
                                }, {
                                    offset: 1,
                                    color: this.echartsData.item?this.echartsData.item.changeColor[1]:''
                                }]),
                                borderColor: '#fff',
                                borderWidth: 2,

                            }
                        },
                        label: {
                            normal: {
                                formatter: this.echartsData.item?this.echartsData.item.proportionType:'',
                                textStyle: {
                                    fontSize: this.echartsData.item.smallPic?12:14,
                                    color: '#666',
                                    fontWeight: 'bold'
                                },
                                padding:[22, 4,12, 4],
                            }
                        }
                    }
                ]
            }]
        };
      this.myChart.on('click',(params) =>{
          this.$emit('handleCharts',params.name)
      })
      this.myChart.setOption(option);

    }

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



