<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
  export default {
  name: 'twoBar',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
        xAxisData:this.echartsData.xAxisData,
        legendData:this.echartsData.legendData,
        seriesData:this.echartsData.seriesData,
    }
  },
  watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
  },
  mounted() {
    this.$nextTick(()=>{
         this.myChart = this.$echarts.init(this.$refs.chart);
         this.initChart()
    })
  },
  methods: {
   initChart() {
      let option = {
            // backgroundColor:'#011337',
            title:{
                text:'利润增长趋势',
                top:15,
                left:15,
                textStyle:{
                    color:'#333',
                    fontSize:14
                }
            },
            grid:{
                    top:90,
                    left:70,
                    right:60,
                    bottom:60
            },
            tooltip: {
                trigger: 'axis',
                backgroundColor:'rgba(255,255,255,.9)',
                padding:10,
                borderColor:'rgba(211,221,231,.9)',
                borderWidth:1,
                textStyle:{
                    color:'#666'
                },
                axisPointer: { // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow', // 默认为直线，可选为：'line' | 'shadow'
                    shadowStyle:{
                        color:'rgba(211,221,231,.9)',
                        shadowBlur: 0,
                        shadowColor:'rgba(211,221,231,.9)',
                        shadowOffsetY:-5
                    },
                    z:0
                },
            },
            legend: {
                top:40,
                icon:'roundRect',
                itemWidth:12,
                itemHeight:7,
                textStyle:{
                    color:'#8199c3'
                },
                data:this.legendData
            },
            xAxis: [
                {
                    type: 'category',
                    axisPointer: {
                        type: 'shadow'
                    },
                    nameTextStyle:{color:'#8199c3'},
                    splitLine:{ show:false },
                    axisTick:{ show:false },
                    axisLabel:{ color:'#6a6a6a' },
                    axisLine:{
                        lineStyle:{
                            color:"#ccc"
                        }
                    },
                    data: this.xAxisData
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    name: '单位（万元）',
                    nameGap:20,
                    nameTextStyle:{color:'#6a6a6a'},
                    splitLine:{ 
                        show:true,
                        lineStyle:{
                            color:'#cdd8e1'
                        }
                    },
                    axisTick:{ show:false },
                    axisLabel:{ color:'#6a6a6a' },
                    axisLine:{
                        lineStyle:{
                            color:"#ccc"
                        }
                    }
                }
            ],
            series: [
                {
                    name:this.legendData[0],
                    type:'bar',
                    // barWidth:13,
                    barMaxWidth:30,
                    barGap:'5%',
                    markArea:{
                        silent:true,
                        data:[
                            [{
                            name: '',
                            xAxis:this.xAxisData[-1],
                            itemStyle: {
                                normal: {
                                    color: new this.$echarts.graphic.LinearGradient(
                                        0, 0, 0, 1,
                                        [
                                            {offset: 0, color: 'rgba(255,255,255,.5)'},
                                            {offset: 1, color: 'rgba(229,243,255,.5)'}
                                        ]
                                    )
                                }
                            },
                        }, {
                            xAxis:this.xAxisData[4],
                        }]
                            
                        ]
                    },
                    itemStyle:{
                        normal:{
                            color: (d)=> {
                                if (d.data > 0) {
                                    return new this.$echarts.graphic.LinearGradient(
                                        0, 0, 0, 1, [{
                                            offset: 0,
                                            color: '#ff9a16'
                                        }, {
                                            offset: 1,
                                            color: '#ff8212'
                                        }]
                                    )
                                } else {
                                    return new this.$echarts.graphic.LinearGradient(
                                        0, 0, 0, 1, [{
                                            offset: 0,
                                            color: '#ff9a16'
                                        }, {
                                            offset: 1,
                                            color: '#ff8212'
                                        }]
                                    )
                                }
                            },
                            // borderColor:'#005ec8',
                            // barBorderRadius:5
                        }
                    },
                    data:this.seriesData[0]
                },
                {
                    name:this.legendData[1],
                    type:'bar',
                    // barWidth:13,
                    barMaxWidth:30,
                    barGap:'5%',
                    itemStyle:{
                        normal:{
                            color: (d)=> {
                                if (d.data > 0) {
                                    return new this.$echarts.graphic.LinearGradient(
                                        0, 0, 0, 1, [{
                                            offset: 0,
                                            color: '#18a4e5'
                                        }, {
                                            offset: 1,
                                            color: '#2881c8'
                                        }]
                                    )
                                } else {
                                    return new this.$echarts.graphic.LinearGradient(
                                        0, 0, 0, 1, [{
                                            offset: 0,
                                            color: '#18a4e5'
                                        }, {
                                            offset: 1,
                                            color: '#2881c8'
                                        }]
                                    )
                                }
                            },
                            // borderColor:'#b08c1e',
                            // barBorderRadius:5
                        }
                    },
                    data:this.seriesData[1]
                }
            ]
      };
      this.myChart.on('click',(params) =>{
          this.$emit('handleCharts',params.name)
      })
      this.myChart.setOption(option);

    }

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



